//Модули
import React, {useState} from "react";
// Стили
import "./QRGenerator.css";

export default function QRGenerator() {
  const [qrVisible, setQrVisible] = useState(false); // Состояние для раскрытия списка QR-кода
  const [inputValue, setInputValue] = useState("");
  const [transformedCode, setTransformedCode] = useState("");
  const [qrCodeUrl, setQrCodeUrl] = useState("");
  const [selectedTransformation, setSelectedTransformation] = useState("transformCode1");

  const toggleQrVisibility = () => {
    setQrVisible((prevState) => !prevState);
  };

  // Функция для генерации DataMatrix QR-кода
  const generateDataMatrix = (text) => {
    const baseUrl = "https://barcode.tec-it.com/barcode.ashx?";
    const params = new URLSearchParams({
      data: text,
      code: "DataMatrix",
      "translate-esc": "on",
      dmsize: "Default",
    });

    return `${baseUrl}${params}`;
  };

  // Функция для преобразования кода маркировки
  const transformCode1 = (code) => {
    const FNC1 = "\\F"; // Код для FNC1
    const GS = "\\x1D"; // Код для GS

    // Извлекаем необходимые части строки
    const left31 = code.substring(0, 31); // Первые 31 символ
    const left37 = code.substring(0, 37); // Первые 37 символов
    const tailFromSecondGS = code.substring(37); // Хвост от 37 символа
    const middleBetweenGS = left37.substring(31); // Символы между 31 и 37

    // Формируем итоговую строку
    return `${FNC1}${left31}${GS}${middleBetweenGS}${GS}${tailFromSecondGS}`;
  };

  const transformCode2 = (code) => {
    const FNC1 = "\\F"; // Код для FNC1
    const GS = "\\x1D"; // Код для GS

    // Извлекаем необходимые части строки
    const left31 = code.substring(0, 31); // Первые 31 символ
    const right31 = code.substring(31);
    const first6OfRight = right31.substring(0, 6); // Первые 6 символов правой части
    const remainingRight = right31.substring(6); // Остаток правой части

    // Формируем итоговую строку
    return `${left31}${FNC1}${first6OfRight}${GS}${remainingRight}`;
  };

  const transformCodeMilk = (code) => {
    const FNC1 = "\\F"; // Код для FNC1
    const GS = "\\x1D"; // Код для GS

    // Извлекаем необходимые части строки
    const leftPart = code.substring(0, 24); // Первые 24 символа
    const tailPart = code.substring(24); // Правая часть после 24 символа

    // Формируем итоговую строку
    return `${FNC1}${leftPart}${GS}${tailPart}`;
  };

  // Обработчик отправки формы
  const handleSubmit = (e) => {
    e.preventDefault();

    // Выбор функции преобразования на основе выбранного генератора
    let transformFunction;
    switch (selectedTransformation) {
      case "transformCode1":
        transformFunction = transformCode1;
        break;
      case "transformCode2":
        transformFunction = transformCode2;
        break;
      case "transformCodeMilk":
        transformFunction = transformCodeMilk;
        break;
      default:
        transformFunction = transformCode1;
    }

    const transformed = transformFunction(inputValue); // Преобразование кода
    setTransformedCode(transformed);
    const qrUrl = generateDataMatrix(transformed); // Генерация QR-кода
    setQrCodeUrl(qrUrl);
  };

  return (
    <>
      <div className="paragraph__container inline" onClick={toggleQrVisibility}>
        <p>Генерация QR-кода</p>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className={`arrow-icon ${qrVisible ? "open" : ""}`}
          width="8"
          height="14"
          viewBox="0 0 8 14"
          strokeWidth="2"
          fill="none"
        >
          <path d="M7 1L1 7L7 13" stroke="#F6F6F6" />
        </svg>
      </div>
      <div className="list__container">
        {qrVisible && (
          <ul>
            <li className="list__item QR">
              <form onSubmit={handleSubmit}>
                <select
                  className="QR__select"
                  value={selectedTransformation}
                  onChange={(e) => setSelectedTransformation(e.target.value)}
                >
                  <option className="QR__option" value="transformCode1">
                    Генератор №1
                  </option>
                  <option className="QR__option" value="transformCode2">
                    Генератор №2
                  </option>
                  <option className="QR__option" value="transformCodeMilk">
                    Генератор для молока
                  </option>
                </select>

                <input
                  type="text"
                  className="report__input"
                  value={inputValue}
                  onChange={(e) => setInputValue(e.target.value)}
                  placeholder="Введите код"
                />

                <button className="report__button" type="submit">
                  Сгенерировать
                </button>
              </form>

              {transformedCode && (
                <div style={{marginTop: "20px"}}>
                  {qrCodeUrl && (
                    <img
                      src={qrCodeUrl}
                      alt="QR Code"
                      style={{width: "150px", height: "150px", border: "20px solid white"}}
                    />
                  )}
                </div>
              )}
            </li>
          </ul>
        )}
      </div>
    </>
  );
}
