//Модули
import React from "react";
// Компоненты
import Counter from "../../components/Counter/Counter";
import ResetCountersButton from "../../components/ResetCountersButton/ResetCountersButton";
import ListItem from "../../Components/ListItem/ListItem";
import QRGenerator from "../../Components/QRGenerator/QRGenerator";

export default function TasksPage() {
  return (
    <section className="counter__section FP">
      <ResetCountersButton
        counterIds={[
          "VSD",
          "SDIZ",
          "CHZ",
          "Marked",
          "NKMT",
          "Ordered",
          "Entered",
          "Verified",
          "Return",
          "Withdrawn",
          "documents",
          "documents__accepted",
          "documents__rejected",
          "merk",
          "Kaiten",
          "Specialists",
        ]}
      />
      <div className="counters__container">
        <div className="thematic__container">
          <div className="container__FP">
            ВСД
            <Counter id="VSD" />
          </div>
          <div className="container__FP">
            СДИЗ
            <Counter id="SDIZ" />
          </div>
          <div className="container__FP">
            ЧЗ
            <Counter id="CHZ" />
          </div>
        </div>

        <div className="container__FP">
          Обработанные документы
          <Counter id="documents" targetIds={["documents__accepted", "documents__rejected"]} />
        </div>
        <div className="thematic__container">
          <div className="container__FP">
            Принято
            <Counter id="documents__accepted" />
          </div>
          <div className="container__FP">
            На уточнение
            <Counter id="documents__rejected" />
          </div>

          <ListItem />

          <QRGenerator />
        </div>

        <div className="thematic__container">
          <div className="container__FP">
            Создано карточек
            <Counter id="NKMT" />
          </div>
          <div className="container__FP">
            Заказано кодов
            <Counter id="Ordered" />
          </div>
          <div className="container__FP">
            Введено в оборот
            <Counter id="Entered" />
          </div>
          <div className="container__FP">
            Проверено кодов
            <Counter id="Verified" />
          </div>
        </div>

        <div className="thematic__container">
          <div className="container__FP">
            Возврат в оборот
            <Counter id="Return" />
          </div>
          <div className="container__FP">
            Выведено из оборота
            <Counter id="Withdrawn" />
          </div>
        </div>

        <div className="container__FP">
          Запросы по ЧЗ и Меркурию
          <Counter id="merk" targetIds={["Specialists", "Kaiten"]} />
        </div>
        <div className="thematic__container">
          <div className="container__FP">
            Кайтен
            <Counter id="Kaiten" />
          </div>
          <div className="container__FP">
            Специалисты
            <Counter id="Specialists" />
          </div>
        </div>
      </div>
    </section>
  );
}
