//Модули
import React, {useState} from "react";
// Стили
import "./ListItem.css";

export default function ListItem() {
  const [ListVisible, setListVisible] = useState(false);
  const toggleListVisibility = () => {
    setListVisible((prevState) => !prevState);
  };

  const [isIconChanged, setIsIconChanged] = useState(null);
  const [optionsByReason, setOptionsByReason] = useState({
    "Номер УПД":
      "Документ сформирован некорректно. Номер УПД не соответствует номеру акта приема передач. Необходимо сделать новый документ.",
    "Вид ТО для УПД":
      "УПД сформирован некорректно, а именно при формирование документа была выбрана не верная функция документа, связи с чем документ исправить не получиться. Необходимо создать новый документ указать корректную функцию документа и выбрать вид товарооборота 'Передача агенту'.",
    "Вид ТО для Накладной":
      "УПД сформирован с ошибкой. Указан некорректный вид товарооборота. Принимаем документы только с видом товарооборота 'Передача агенту'. Необходимо внести  исправление.",
    "Количество товаров":
      "УПД сформирован некорректно, а именно, количество подконтрольных товаров в накладной не соответствует количеству кодов в УПД.",
    "Причина убытия":
      "Документ некорректный. Необходимо передать новый документ без указания причины убытия.",
    "Статус 'Отмена'": "Документ не актуальный, накладная в статусе 'Отмена'.",
    "В названии нет 'ЦПТ'":
      "Документ сформирован некорректно, в наименовании отсутствует указывающий пизнак ЦПТ. Необходимо создать новый документ (УПД).",
    "Формат КМ":
      "УПД обработан с ошибкой  Коды маркировки в УПД  должны передаваться в формате первых 31 символов до цифры «91». необходимо исправление через УПД(и).",
    "КМ не в обороте": "Документ сформирован некорректно. Коды маркировки не в обороте.",
    "Документ дублируется":
      "Документ сформирован некорректно. Документ с таким же УПД уже был создан.",
    "Тип упаковки":
      "УПД сформирован некорректно. Коды маркировки передаются на единицу товара, а должен быть 'Тип упаковки: Комплект'.",
    "Нет полномочий":
      "Документ сформирован некорректно. Нет полномочий на выполнение операции с Кодом Маркировки.",
    "Документ с ошибками":
      "Документ обработан с ошибками. Подробнее можете посмотреть во вкладке документы, исходящие. Найти данный документ и посмотреть отчёт об ошибках. Документ необходимо исправить.",
  });

  // Функция для копирования полного текста
  const handleCopyText = (fullText, index) => {
    navigator.clipboard.writeText(fullText);

    setIsIconChanged(index);

    setTimeout(() => {
      setIsIconChanged(null);
    }, 2000);
  };

  return (
    <>
      <div className="paragraph__container inline" onClick={toggleListVisibility}>
        <p>Причины уточнения</p>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className={`arrow-icon ${ListVisible ? "open" : ""}`}
          width="8"
          height="14"
          viewBox="0 0 8 14"
          strokeWidth="2"
          fill="none"
        >
          <path d="M7 1L1 7L7 13" stroke="#F6F6F6" />
        </svg>
      </div>
      <div className="list__container">
        {ListVisible && (
          <ul>
            {Object.keys(optionsByReason).map((shortText, index) => (
              <li key={shortText} className="list__item">
                <span className="list__label" title={optionsByReason[shortText]}>
                  {shortText}
                </span>

                {/* Кнопка копирования */}
                <div
                  className="copy__button"
                  onClick={() => handleCopyText(optionsByReason[shortText], index)}
                >
                  <svg
                    width="20"
                    height="24"
                    viewBox="0 0 20 24"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M4.66667 4.4375V3.44444C4.66667 2.09442 5.76108 1 7.11111 1H15.6667C17.0167 1 18.1111 2.09442 18.1111 3.44444V16.8889C18.1111 18.2389 17.0167 19.3333 15.6667 19.3333H14.4444M1 7.11111V20.5556C1 21.9056 2.09442 23 3.44444 23H12C13.35 23 14.4444 21.9056 14.4444 20.5556V7.11111C14.4444 5.76108 13.35 4.66667 12 4.66667H3.44444C2.09442 4.66667 1 5.76108 1 7.11111Z"
                      stroke={isIconChanged === index ? "#16ff65" : "#F6F6F6"} // Изменение цвета иконки при копировании
                    />
                  </svg>
                </div>
              </li>
            ))}
          </ul>
        )}
      </div>
    </>
  );
}
