const ImageDownloader = () => {
  const [sheetData, setSheetData] = useState([]);
  const spreadsheetId = "1kXuikeb7rfZ8piBAEQhRq3F8JgZI0WYYbWuYmR6SjWA";
  const sheetName = process.env.REACT_APP_SHEET_NAME;

  // Функция для получения данных из Google Sheets
  const fetchReportData = () => {
    chrome.runtime.sendMessage(
      {
        action: "fetchGoogleSheetsData",
        spreadsheetId: spreadsheetId,
        sheetName: sheetName,
      },
      (response) => {
        if (response.success) {
          setSheetData(response.data); // Сохраняем данные в состояние
        } else {
          console.error("Ошибка при получении данных:", response.error);
        }
      }
    );
  };

  // Функция для скачивания и применения эффекта негатива
  const handleDownload = async (imageUrl, fileName) => {
    // Создаем новое изображение
    const img = new Image();
    img.src = imageUrl;

    img.onload = function () {
      // Создаем канвас для обработки изображения
      const canvas = document.createElement("canvas");
      const ctx = canvas.getContext("2d");
      canvas.width = img.width;
      canvas.height = img.height;

      // Рисуем изображение на канвасе
      ctx.drawImage(img, 0, 0);

      // Получаем данные пикселей
      const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
      const data = imageData.data;

      // Применяем эффект негатива (инвертируем цвета)
      for (let i = 0; i < data.length; i += 4) {
        data[i] = 255 - data[i]; // Красный
        data[i + 1] = 255 - data[i + 1]; // Зеленый
        data[i + 2] = 255 - data[i + 2]; // Синий
      }

      // Записываем измененные данные обратно на канвас
      ctx.putImageData(imageData, 0, 0);

      // Преобразуем канвас обратно в DataURL
      const invertedImageUrl = canvas.toDataURL("image/png");

      // Создаем ссылку для скачивания
      const link = document.createElement("a");
      link.href = invertedImageUrl;
      link.download = fileName + ".png"; // Имя файла из столбца F
      link.click();
    };
  };

  useEffect(() => {
    fetchReportData();
  }, []);

  return (
    <>
      <table className="sheet__table">
        <tbody>
          {sheetData.map((row, index) => {
            // Извлекаем ссылки на изображения из столбца H и название из столбца F
            const imageUrl = row[7]; // Столбец H (ссылка на изображение)
            const fileName = row[5]; // Столбец F (название файла)

            return (
              <tr key={index}>
                <td>
                  <a href={imageUrl}>{fileName}</a>
                </td>
                <td>
                  <button onClick={() => handleDownload(imageUrl, fileName)}>
                    Скачать и применить негатив
                  </button>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </>
  );
};

const DataFetcher = () => {
  const [startRow, setStartRow] = useState(2); // Стартовая строка для начала обработки

  const handleProcessLinks = () => {
    chrome.runtime.sendMessage({
      type: "processLinks",
      data: {
        spreadsheetId: "1iy26IyafkroRI-uwr7WsBg9NwydCiCqVtdZv3KUOnuE",
        sheetName: "Лист1",
        startRow,
      },
    });
  };

  return (
    <div>
      <label>
        Номер строки для начала:
        <input
          type="number"
          value={startRow}
          onChange={(e) => setStartRow(Number(e.target.value))}
        />
      </label>
      <button onClick={handleProcessLinks}>Начать</button>
    </div>
  );
};

