// Модули
import React, {useContext, useEffect, useState} from "react";
// Компоненты
import {CounterContext} from "../../components/Context/CounterContext";
//Стили
import "./ReportPage.css";

export default function ReportPage() {
  const {counters} = useContext(CounterContext);
  const [name, setName] = useState(localStorage.getItem("username") || "");
  const [isNameEntered, setIsNameEntered] = useState(false);
  const [report, setReport] = useState("");
  const currentDate = new Date().toLocaleDateString();

  useEffect(() => {
    setIsNameEntered(name.trim() !== "");
  }, [name]);

  useEffect(() => {
    localStorage.setItem("username", name);
  }, [name]);

  const handleNameChange = (event) => {
    const enteredName = event.target.value;
    setName(enteredName);
    setIsNameEntered(enteredName.trim() !== ""); // Устанавливаем флаг isNameEntered, если введено имя
  };

  const generateReport = () => {
    if (!name.trim()) {
      return;
    }

    let reportText = `/#отчет_МГСП_${name} ${currentDate}\n`;

    const groups = {
      tasks_group: [
        {text: "**1. ВСД:**", id: "VSD"},
        {text: "**2. СДИЗ:**", id: "SDIZ"},
        {text: "**3. ЧЗ:**", id: "CHZ"},
        {text: "**4. Перемаркировано**"},
        {text: "4.1. Формирование карточек НКМТ:", id: "NKMT"},
        {text: "4.2. Заказано кодов:", id: "Ordered"},
        {text: "4.3. Введено в оборот:", id: "Entered"},
        {text: "**5. Проверено кодов:**", id: "Verified"},
        {text: "**6. Возврат в оборот:**", id: "Return"},
        {text: "**7. Выведено из оборота:**", id: "Withdrawn"},
        {text: "**8. Обработанные документы:**", id: "documents"},
        {text: "Из них принято:", id: "documents__accepted"},
        {text: "Отправлено на уточнение:", id: "documents__rejected"},
        {text: "**9. Запросы по ЧЗ и Меркурию:**", id: "merk"},
      ],
    };

    // Проходимся по каждой группе
    Object.keys(groups).forEach((groupKey) => {
      const group = groups[groupKey];

      group.forEach((item) => {
        const counter = counters.find((counter) => counter.id === item.id);
        if (counter) {
          reportText += `${item.text} ${counter.value}\n`;
        } else if (!item.id) {
          reportText += `${item.text}\n`;
        }
      });
    });

    setReport(reportText);
    copyReport(reportText);
  };

  const copyReport = (reportText) => {
    if (reportText) {
      navigator.clipboard.writeText(reportText).catch(() => {});
    }
  };

  return (
    <section className="report__section">
      <div className="label__container">
        <label className="report__label">
          Имя:
          <input
            className="report__input"
            type="text"
            id="username"
            name="username"
            placeholder="Введите Ваше имя"
            value={name}
            onChange={handleNameChange}
          />
        </label>
      </div>

      <div className="button__container">
        <button className="report__button" onClick={generateReport} disabled={!isNameEntered}>
          Создать
        </button>
        <button className="report__button" onClick={() => copyReport(report)} disabled={!report}>
          Копировать
        </button>
      </div>

      <pre className="report__text">{report}</pre>
    </section>
  );
}
